<script>
import { formatPrice } from "@/filters/index";
export default {
  name: "OrderDetail",
  filters: {
    formatPrice
  },
  data() {
    const labels = {
      orderId: "",
      status: "",
      goodsName: "",
      sellAmount: "",
      quantity: "",
      retailPrice: "",
      discountPrice: "",
      agreedPrice: "",
      customerName: "",
      customerPhone: "",
      vehicleNum: "",
      vehicleModel: "",
      logisticsName: "",
      oilStationName: "",
      oilStationAddress: "",
      createTime: "",
      effectTime: ""
    };
    return {
      labels
    };
  },
  computed: {
    form() {
      return this.$store.getters.orderDetail.form;
    },
    goods() {
      return this.form.goods || {};
    },
    customer() {
      return this.form.customer || {};
    },
    logistics() {
      return this.form.logistics || {};
    },
    vehicle() {
      return this.form.vehicle || {};
    },
    oilStation() {
      return this.form.oilStation || {};
    }
  },
  created() {
    const query = this.$route.query;
    if (query.orderId) {
      this.$store.dispatch("order-detail:fetch-form");
    }
  }
};
